input {
    width: 100%;
    font-size: 1.2rem;
    margin-bottom: 50px;
}

.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    text-align: center;
}

.card img {
    max-height: 150px;
}

.card-grid {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}